:root {
    --primary: #2A2A2A;
    --secondary: #FFFFFF;
    --contrast: #ff851b;
}

.btn {
    padding: 8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;
}

.btn--primary{
    background-color: var(--primary);
    color: var(--secondary);
    border: 1px solid var(--primary);
}

.btn--outline{
    background-color: transparent;
    color: var(--contrast);
    padding: 8px 20px;
    border: 1px solid var(--contrast);
    transition: all 0.3s ease-out;
}

.btn--medium{
    padding: 8px 20px;
    font-size: 20px;
}

.btn--large{
    padding: 12px 26px;
    font-size: 20px;
}

.btn--medium:hover, .btn--large:hover{
    background-color: var(--secondary);
    color: var(--primary);
    border: 1px solid var(--contrast);
    transition: all 0.3s ease-out;
}